import { Component, OnInit } from '@angular/core';
import { Authorization } from 'core/services/authorization.service';
import Utility from 'shared/helpers/utility';
import { NAV_MENU } from './full-layout.navigation';
import { UserService } from 'modules/user/user.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './full-layout.component.html',
  providers: [Authorization, UserService]
})
export class FullLayoutComponent implements OnInit {

  public disabled: boolean = false;
  public userName: string;
  public userEmail: string;
  public navMenu: Object[];

  public sidebarMinimized = true;
  private changes: MutationObserver;
  public element: HTMLElement = document.body;

  private logoutUrl = '/logout';

  constructor(
    private authorization: Authorization,
    private userService: UserService,
    private router: Router,
  ) {

    this.userName = localStorage.getItem('userName');
    this.userEmail = localStorage.getItem('userEmail');

    this.changes = new MutationObserver((mutations) => {
      this.sidebarMinimized = document.body.classList.contains('sidebar-minimized')
    });

    this.changes.observe(<Element>this.element, {
      attributes: true
    });
  }

  async ngOnInit() {

    const nav_menu = Utility.copy(NAV_MENU);
    this.navMenu = await this._prepareNavigation(nav_menu);
  }

  private async _prepareNavigation(nav_menu) {

    let hasAccess, children;
    const accessMenu = [];

    for (const item of nav_menu) {

      if (item.children === undefined) {

        hasAccess = await this.authorization.hasAccess(item.access, item.url.substring(1));
        if (hasAccess) {

          delete item.access;
          accessMenu.push(item);
        }

      } else {

        children = await this._prepareNavigation(item.children);
        if (children.length > 0) {

          item.children = children;
          accessMenu.push(item);
        }
      }
    }

    return accessMenu;
  }

  logout() {

    this.userService.logout();
    this.router.navigateByUrl(this.logoutUrl);
  }
}
