import { Component } from '@angular/core';

import { UserService } from 'modules/user/user.service';

class ForgotPassword {
  email_id: string = '';
}

@Component({
  selector: 'app-root',
  templateUrl: './forgot-password.component.html',
  providers: [UserService]
})
export class ForgotPasswordComponent {

  data: ForgotPassword = new ForgotPassword();
  error: string = '';
  success: boolean = false;

  constructor(
    private userService: UserService
  ) {
  }

  resetPassword() {

    if (!this.data.email_id) {
      this.error = 'Please provide E-Mail ID for resetting password';
      return;
    }

    this.userService.forgotPassword(this.data.email_id)
      .then(response => {
        if (response.status === true) {
          this.success = true;
        };
      })
      .catch(err => {
        this.error = err.error.message.text;
      })
  }
}
