import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';

import { environment } from '../../../environments/environment'

@Injectable()
export class AppHttp {

  protected headers: HttpHeaders;

  constructor(protected http: HttpClient) {

    this.http = http;
    this.headers = new HttpHeaders({
      'Content-Type': 'application/json',
      'Accept': 'application/json'
    });

    if (localStorage.getItem('sessionToken') !== null) {
      this.headers = this.headers.delete('x-session-token');
      this.headers = this.headers.set('x-session-token', localStorage.getItem('sessionToken'));
    }

    if (sessionStorage.getItem('accessToken') !== null) {
      this.headers = this.headers.delete('x-access-token');
      this.headers = this.headers.set('x-access-token', sessionStorage.getItem('accessToken'));
    }
  }

  protected resetHeaders() {

    this.headers = this.headers.delete('x-session-token');
    this.headers = this.headers.delete('x-access-token');
  }

  protected prepareParameters(query?: Map<string, any>): HttpParams {

    let params = new HttpParams();
    if (typeof query === 'undefined') {
      return params;
    }

    query.forEach(function (value, key) {
      params = params.set(key, value);
    });
    return params;
  }

  protected handleError(error: any, url: string, data?: any) {

    console.error('URL: ' + url);
    console.error('Data: ', data);
    console.error('Error: ', error);

    return Promise.reject(error);
  }

  get(url: string, query?: Map<string, any>): Promise<any> {

    return this.http.get(environment.apiServer + url, {
      headers: this.headers,
      params: this.prepareParameters(query)
    }).toPromise()
      .catch((error) => this.handleError(error, url, { query: query }));
  }

  put(url: string, data: any, query?: Map<string, any>): Promise<any> {

    return this.http.put(environment.apiServer + url, data, {
      headers: this.headers,
      params: this.prepareParameters(query)
    }).toPromise()
      .catch((error) => this.handleError(error, url, { query: query, data: data }));
  }

  post(url: string, data: any, query?: Map<string, any>): Promise<any> {

    return this.http.post(environment.apiServer + url, data, {
      headers: this.headers,
      params: this.prepareParameters(query)
    }).toPromise()
      .catch((error) => this.handleError(error, url, { query: query, data: data }));
  }

  patch(url: string, data?: any, query?: Map<string, any>): Promise<any> {

    return this.http.request('patch', environment.apiServer + url, {
      headers: this.headers,
      params: this.prepareParameters(query),
      body: data
    }).toPromise()
      .catch((error) => this.handleError(error, url, { query: query }));
  }

  delete(url: string, data?: any, query?: Map<string, any>): Promise<any> {

    return this.http.request('delete', environment.apiServer + url, {
      headers: this.headers,
      params: this.prepareParameters(query),
      body: data
    }).toPromise()
      .catch((error) => this.handleError(error, url, { query: query }));
  }
}
