import { Component } from '@angular/core';

import { OrganizationService } from 'modules/organization/organization.service';
import { OtpService } from 'core/services/otp.service';

const ERROR_MAP = {
  'first': 'First Name',
  'middle': 'Middle Name',
  'last': 'Last Name',
  'email_id': 'E-Mail ID'
};

class Name {

  first: string;
  middle: string;
  last: string;
}

class Admin {

  name: Name;
  email_id: string;

  constructor() {
    this.name = new Name();
  }
}

class Register {

  name: string = '';
  admin: Admin;
  otp: string = '';

  constructor() {
    this.admin = new Admin();
  }
}

@Component({
  selector: 'app-root',
  templateUrl: './register.component.html',
  providers: [OtpService, OrganizationService]
})
export class RegisterComponent {

  data: Register = new Register();

  error: string = '';
  success: boolean = false;
  otpSent: boolean = false;

  invalidAttributes: Array<string> = [];

  constructor(
    private organizationService: OrganizationService,
    private otpService: OtpService) {
  }

  register() {

    this.error = '';
    this.invalidAttributes = [];

    this.organizationService.postOrg(this.data).then(() => {
      this.success = true;
    }).catch(err => {

      this.error = err.error.message.text;
      if (err.error.invalid_attributes) {
        for (const key in err.error.invalid_attributes) {
          if (err.error.invalid_attributes.hasOwnProperty(key)) {
            this.invalidAttributes.push(ERROR_MAP[key] ? ERROR_MAP[key] : err.error.invalid_attributes[key]);
          }
        }
      }

    });
  }

  sendOtp() {

    if (!this.data.admin.email_id) {
      this.error = 'Provide E-Mail ID before sending OTP';
      return;
    }

    this.error = '';
    this.otpService.sendOtp('organization', { email_id: this.data.admin.email_id })
      .then(response => {

        if (response.status === true) {
          this.otpSent = true;
        }

      });
  }
}
