import { Injectable } from '@angular/core';
import { environment } from '../../../../environments/environment';

@Injectable()
export class Google {

  public constructor() {

    ga('create', environment.google.analytics.trackingId, 'auto');
  }

  public pageview(url: string) {

    ga('set', 'page', url);
    ga('send', 'pageview');
  }

  public event(
    category: string,
    action: string,
    label: string = null,
    value: number = null
  ) {

    ga('send', 'event', {
      eventCategory: category,
      eventAction: action,
      eventLabel: label,
      eventValue: value
    });
  }
}
