import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Api } from 'core/services/api.service';
import { Authorization } from 'core/services/authorization.service';

@Injectable()
export class UserService extends Api {

  private _base_url: string = '/users';

  constructor(http: HttpClient) {

    super(http);
  }

  login(email_id: string, password: string) {

    this.resetLocalStorage();
    this.resetSessionStorage();

    const data = {
      'email_id': email_id,
      'password': password
    };

    return this.post(this._base_url + '/login', data)
      .then(result => {

        result = result['data'];

        localStorage.setItem('userId', result.user_id);
        localStorage.setItem('userName', result.name);
        localStorage.setItem('userEmail', result.email_id);
        localStorage.setItem('sessionToken', result.session_token);

        this.resetHeaders();

        return data;
      }
    );
  }

  logout() {

    this.resetLocalStorage();
    this.resetSessionStorage();
    Authorization.resetAccess();
    this.resetHeaders();
  }

  forgotPassword(email_id: string) {

    return this.get(this._base_url + '/resetPassword?email_id=' + email_id)
      .then(response => response);
  }

  resetPassword(resetPasswordKey: string, password: string) {

    const data = {
      'reset_password_key': resetPasswordKey,
      'password': password
    };

    return this.put(this._base_url + '/resetPassword', data)
      .then(response => response['data']);
  }

  setPassword(setPasswordKey: string, password: string) {

    const data = {
      'set_password_key': setPasswordKey,
      'password': password
    };

    return this.post(this._base_url + '/setPassword', data)
      .then(response => response['data']);
  }
}
