import { Injectable } from '@angular/core';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { HttpClient } from '@angular/common/http';

import { AppHttp } from './app-http.service';

class AccessibleEntity {
  entity: string;
  action?: string;
}

@Injectable()
export class Authorization extends AppHttp {

  private static profile_access = {};

  private _access_url: string = '/profile/access';
  private global_routes = [
    'dashboard'
  ];

  static resetAccess() {

    Authorization.profile_access = {};
  }

  constructor(protected http: HttpClient) {
    super(http);
  }

  private _loadAccess() {

    if (Object.keys(Authorization.profile_access).length > 0) {
      return new Promise(resolve => resolve(true));
    }

    this.resetHeaders();

    if (localStorage.getItem('sessionToken') !== null) {
      this.headers = this.headers.delete('x-session-token');
      this.headers = this.headers.set('x-session-token', localStorage.getItem('sessionToken'));
    }

    if (sessionStorage.getItem('accessToken') !== null) {
      this.headers = this.headers.delete('x-acess-token');
      this.headers = this.headers.set('x-access-token', sessionStorage.getItem('accessToken'));
    }

    return this.get(this._access_url)
      .then(response => {

        response = response.data;
        const profile_access = {};
        response.forEach(function (module) {
          module.entities.forEach(function (entity) {

            const _module = module.module.replace(' ', '_'),
              _entity = entity.entity.replace(' ', '_');

            profile_access[_module + ':' + _entity] = [];
            entity.actions.forEach(function (action) {
              profile_access[_module + ':' + _entity].push(action);
            });
          });
        });

        Authorization.profile_access = profile_access;
        return true;
      });
  }

  hasAccess(access: AccessibleEntity, path?: string) {

    if (Object.keys(Authorization.profile_access).length > 0) {

      if (path !== undefined && this.global_routes.indexOf(path) !== -1) {
        return true;
      } else if (access === undefined || access.entity === undefined) {
        return false;
      }
    }

    return this._loadAccess().then(response => {

      if (path !== undefined && this.global_routes.indexOf(path) !== -1) {
        return true;
      } else if (access === undefined || access.entity === undefined) {
        return false;
      } else if (Authorization.profile_access[access.entity] === undefined) {
        return false;
      } else if (access.action === undefined) {
        return true;
      } else if (Authorization.profile_access[access.entity].indexOf(access.action) === -1) {
        return false;
      }

      return true;
    });
  }
}

@Injectable()
export class AuthorizationGuard implements CanActivate {

  constructor(private router: Router, private authorization: Authorization) { }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {

    return this.authorization.hasAccess(route.data.access, route.routeConfig.path);
  }
}
