import { Component } from '@angular/core';

import { Router, ActivatedRoute } from '@angular/router';
import { UserService } from 'modules/user/user.service'

class ResetPassword {
  password: string = '';
  verifyPassword: string = '';
  resetPasswordKey = '';
}

@Component({
  selector: 'app-root',
  templateUrl: './reset-password.component.html',
  providers: [UserService]
})
export class ResetPasswordComponent {

  data: ResetPassword = new ResetPassword();

  passwordError: boolean = false;
  verifyPasswordError: boolean = false;

  error: string = '';
  success: boolean = false;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private userService: UserService
  ) {

    this.data.resetPasswordKey = this.route.snapshot.params['passKey'];
  }

  onPasswordChange(value) {

    this.data.password = value;
    this.passwordError = !this.validatePassword(this.data.password);
  }

  onVerifyPasswordChange(value) {

    this.data.verifyPassword = value;
    this.verifyPasswordError = (this.data.password !== this.data.verifyPassword);
  }

  resetPassword() {

    this.userService.resetPassword(this.data.resetPasswordKey, this.data.password)
      .then(a => {

        this.success = true;
        setTimeout(() => {
          this.router.navigateByUrl('/login');
        }, 4000);

      })
      .catch(err => {
        this.error = 'Some error occurred while setting password.\n' + err.error.message.text;
      })
  }

  private validatePassword(password: string): boolean {

    const passwordRegex = /^(?=.*[a-zA-Z])(?=.*\d)(?=.*[.,_()@*&^%$#!?~:;<>\[\]\-])[a-zA-Z\d.,_()@*&^%$#!?~:;<>\[\]\-]*$/;

    return password.length >= 8 && passwordRegex.test(password);
  }
}
