import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { LocationStrategy, PathLocationStrategy } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { PerfectScrollbarModule, PERFECT_SCROLLBAR_CONFIG, PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';
import { AppAsideModule, AppBreadcrumbModule, AppHeaderModule, AppFooterModule, AppSidebarModule, } from '@coreui/angular';

import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import { TabsModule } from 'ngx-bootstrap/tabs';
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import { TooltipModule } from 'ngx-bootstrap/tooltip';
import { TreeviewModule } from 'ngx-treeview';
import { NgSelectModule } from '@ng-select/ng-select';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './app.routing';

import { ProfileService } from 'modules/user/profile/profile.service';
import { AuthenticationGuard } from 'core/services/authentication.service';
import { Authorization, AuthorizationGuard } from 'core/services/authorization.service';

// Layouts
import { FullLayoutComponent } from './layouts/full-layout.component';
import { LoginComponent } from 'modules/core/login/login.component';
import { LogoutComponent } from 'modules/core/logout/logout.component';
import { RegisterComponent } from 'modules/core/register/register.component';
import { ForgotPasswordComponent } from 'modules/core/forgot-password/forgot-password.component';
import { ResetPasswordComponent } from 'modules/core/reset-password/reset-password.component';
import { SetPasswordComponent } from 'modules/core/set-password/set-password.component';
import { P404Component } from './core/404/404.component';
import { P500Component } from './core/500/500.component';

const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
  suppressScrollX: true
};

@NgModule({
    imports: [
      BrowserModule,
      AppRoutingModule,
      FormsModule,
      HttpClientModule,
      AppAsideModule,
      AppBreadcrumbModule.forRoot(),
      AppFooterModule,
      AppHeaderModule,
      AppSidebarModule,
      PerfectScrollbarModule,
      BsDropdownModule.forRoot(),
      TabsModule.forRoot(),
      BsDatepickerModule.forRoot(),
      TooltipModule.forRoot(),
      TreeviewModule.forRoot(),
      NgSelectModule
    ],
    declarations: [
      AppComponent,
      FullLayoutComponent,
      LoginComponent,
      LogoutComponent,
      RegisterComponent,
      ForgotPasswordComponent,
      ResetPasswordComponent,
      SetPasswordComponent,
      P404Component,
      P500Component
    ],
    providers: [
      {
        provide: LocationStrategy,
        useClass: PathLocationStrategy,
      },
      {
        provide: PERFECT_SCROLLBAR_CONFIG,
        useValue: DEFAULT_PERFECT_SCROLLBAR_CONFIG
      },
      AuthenticationGuard,
      AuthorizationGuard,
      Authorization,
      ProfileService
    ],
    bootstrap: [AppComponent]
})
export class AppModule { }
