import { Injectable } from '@angular/core';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { environment } from '../../../environments/environment'

import { ProfileService } from 'modules/user/profile/profile.service';

@Injectable()
export class AuthenticationGuard implements CanActivate {

  constructor(private router: Router, private profileService: ProfileService) { }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {

    if (!localStorage.getItem('sessionToken')) {
      // not logged in so redirect to login page with the return url and return false

      this.router.navigate(['login'], { queryParams: { returnUrl: state.url } });
      return false;
    }

    const stale_access_token_time = new Date();
    stale_access_token_time.setMinutes(stale_access_token_time.getMinutes() - environment.accessTokenLifetime);

    let access_token_time = undefined;
    if (sessionStorage.getItem('accessTokenTime')) {
      access_token_time = new Date(sessionStorage.getItem('accessTokenTime'));
    }

    if (!sessionStorage.getItem('accessToken') || !access_token_time || access_token_time < stale_access_token_time) {

      return this.profileService.me()
        .then(() => {
          return true;
        })
        .catch(() => {
          this.router.navigate(['login'], { queryParams: { returnUrl: state.url } });
          return false;
        });

    } else {
      return true;
    }
  }
}
