import { Injectable } from '@angular/core';
import { AppHttp } from 'core/services/app-http.service';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class OtpService extends AppHttp {

  private _base_url: string = '/otp/generate';

  constructor(http: HttpClient) {

    super(http);
  }

  sendOtp(type: string, data: any) {

    return this.post(this._base_url + '?type=' + type, data)
      .then(response => response);
  };
}
