import * as diff from 'deep-diff';
import * as clone from 'clone';
import * as has from 'has-value';

export default class Utility {

  public static compare(base: any, data: any): any {
    return diff(base, data);
  }

  public static copy(source: any): any {
    return clone(source);
  }

  public static empty(source: any): boolean {
    return !has(source);
  }
}
