import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { AppHttp } from './app-http.service';

@Injectable()
export class Api extends AppHttp {

  protected headers: HttpHeaders;

  constructor(protected http: HttpClient) {
    super(http);
  }

  private __checkTokens() {

    if (this.headers.has('x-session-token') && this.headers.has('x-access-token')) {
      return;
    }

    if (localStorage.getItem('sessionToken') !== null) {
      this.headers = this.headers.delete('x-session-token');
      this.headers = this.headers.set('x-session-token', localStorage.getItem('sessionToken'));
    }

    if (sessionStorage.getItem('accessToken') !== null) {
      this.headers = this.headers.delete('x-acess-token');
      this.headers = this.headers.set('x-access-token', sessionStorage.getItem('accessToken'));
    }
  }

  protected resetLocalStorage() {

    localStorage.removeItem('userId');
    localStorage.removeItem('userName');
    localStorage.removeItem('userEmail');
    localStorage.removeItem('sessionToken');
  }

  protected resetSessionStorage() {

    sessionStorage.removeItem('accessToken');
    sessionStorage.removeItem('accessTokenTime');
  }

  get(url: string, query?: Map<string, any>): Promise<any> {

    this.__checkTokens();
    return super.get(url, query);
  }

  put(url: string, data: any, query?: Map<string, any>): Promise<any> {

    this.__checkTokens();
    return super.put(url, data, query);
  }

  patch(url: string, data?: any, query?: Map<string, any>): Promise<any> {

    this.__checkTokens();
    return super.patch(url, data, query);
  }

  post(url: string, data: any, query?: Map<string, any>): Promise<any> {

    this.__checkTokens();
    return super.post(url, data, query);
  }

  delete(url: string, data?: any, query?: Map<string, any>): Promise<any> {

    this.__checkTokens();
    return super.delete(url, data, query);
  }
}
