import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { FullLayoutComponent } from './layouts/full-layout.component';
import { LoginComponent } from 'modules/core/login/login.component';
import { LogoutComponent } from 'modules/core/logout/logout.component';
import { RegisterComponent } from 'modules/core/register/register.component';
import { ForgotPasswordComponent } from 'modules/core/forgot-password/forgot-password.component';
import { ResetPasswordComponent } from 'modules/core/reset-password/reset-password.component';
import { SetPasswordComponent } from 'modules/core/set-password/set-password.component';
import { P404Component } from './core/404/404.component';

export const routes: Routes = [
  {
    path: '',
    redirectTo: 'dashboard',
    pathMatch: 'full',
  },
  {
    path: '',
    component: FullLayoutComponent,
    data: {
      title: 'Home'
    },
    loadChildren: './modules/application.module#ApplicationModule'
  },
  {
    path: 'login',
    component: LoginComponent,
    data: {
      title: 'Login Page'
    }
  },
  {
    path: 'logout',
    component: LogoutComponent,
    data: {
      title: 'Logged Out'
    }
  },
  {
    path: 'register',
    component: RegisterComponent,
    data: {
      title: 'Register Page'
    }
  },
  {
    path: 'forgotPassword',
    component: ForgotPasswordComponent,
    data: {
      title: 'Forgot Password'
    }
  },
  {
    path: 'resetPassword/:passKey',
    component: ResetPasswordComponent,
    data: {
      title: 'Reset Password'
    }
  },
  {
    path: 'setPassword/:passKey',
    component: SetPasswordComponent,
    data: {
      title: 'Set Password'
    }
  },
  {
    path: '**',
    component: P404Component,
    data: {
      title: 'Page 404'
    }
  }
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}
